@import '../../../sass/main';

.noMatch {
  @include block-format(_, 100%, 100vh);
  min-height: 100vh;
  background: linear-gradient(-45deg, $white, $purple-ultra-light 100%);

  &_text {
    position: absolute;
    @include flex(_, _, column);
    top: 10%;
    left: 10%;
    font: 30px/1.2 $satisfy-serif;

    :nth-child(1) {
      font: bold 40px/1.2 $satisfy-serif;
    }

    :nth-child(2) {
      font: italic 600 30px/2 $satisfy-serif;
      border-bottom: 1px solid $black;
    }
  }
}